// Image Component (Pengganti next/image)
interface ImageProps {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  className?: string;
  fill?: boolean;
  priority?: boolean;
  quality?: number; // Diabaikan
}

const CustomImage: React.FC<ImageProps> = ({ src, alt, width, height, className = '', fill = false, ...props }) => {
    if (fill) {
        // Mode fill untuk background
        return <img src={src} alt={alt} className={`absolute inset-0 w-full h-full object-cover ${className}`} {...props} />;
    }
    // Mode standar
    return <img src={src} alt={alt} width={width} height={height} className={className} {...props} />;
};

// Button Component (Pengganti components/ui/button)
interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: 'default' | 'secondary' | 'outline';
}

const Button: React.FC<ButtonProps> = ({ children, variant = 'default', className = '', ...props }) => {
  let baseClasses = 'px-4 py-2 rounded-lg font-semibold transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2';
  
  // Menggunakan primary: indigo-500, secondary: gray-600, card/background: gray-800/900
  switch (variant) {
    case 'secondary':
      baseClasses += ' bg-gray-600 text-white hover:bg-gray-700 focus:ring-gray-500';
      break;
    case 'outline':
      baseClasses += ' bg-transparent border-2 border-indigo-400 text-indigo-400 hover:bg-indigo-400/10 focus:ring-indigo-400';
      break;
    case 'default':
    default:
      baseClasses += ' bg-indigo-500 text-white hover:bg-indigo-600 focus:ring-indigo-500';
      break;
  }
  
  return (
    <button className={`${baseClasses} ${className}`} {...props}>
      {children}
    </button>
  );
};

